@import url("https://fonts.googleapis.com/css2?family=Lato&family=Montserrat&family=Mulish:wght@300;400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@500&family=Lato&family=Montserrat&family=Mulish:wght@300;400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@500&family=DM+Mono:wght@300&family=Lato&family=Montserrat&family=Mulish:wght@300;400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@500&family=DM+Mono:wght@300&family=Exo:wght@200&family=Lato&family=Montserrat&family=Mulish:wght@300;400&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

.font-dmmono {
  font-family: "DM Mono", monospace;
}

.font-exo {
  font-family: "Exo", sans-serif;
}

nav {
  font-family: "Lato", sans-serif;
}

.font-cairo {
  font-family: "Cairo", sans-serif;
}

.shadow-right {
  box-shadow: 0 1px 10px 2px rgb(0, 21, 35);
}

.transition-500ms {
  transition: background 500ms;
}

.transition-all {
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 1s;
}

* {
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 1s;
}

.esconder{
  left: -100%;
  opacity: 0%;
}

.mostrar{
  left: 0;
  opacity: 100%;
}

.bg-gradient-to-r {
    background: linear-gradient( 90deg, transparent,  rgb(31 41 55 ) 40%);
}

@media not all and (min-width: 932px) {
    .max-md\:hidden {
        display: none;
    }
    .max-md\:gap-8 {
        gap: 2rem;
    }
}